import React from 'react'
import styled from 'styled-components'
import { Link } from 'gatsby'

const NavigationWrapper = styled.ul`
    padding: 0;
    list-style: none;
    display: flex;
`;

const NavigationItem = styled.li`
    margin-right: 30px;  
    font-weight: 700;
    font-size: 1.5rem;

    a {
        text-decoration: none;
        color: rgba(255, 255, 255, 0.7);
        text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
    }

    a:hover {
        color: rgb(245, 251, 173);
    }
    
`;

const Navigation = () => (
    <NavigationWrapper>
        <NavigationItem><Link to={'/roman/'}>roman</Link></NavigationItem>
        <NavigationItem><Link to={'/gannajestem/'}>gannajestem</Link></NavigationItem>    
    </NavigationWrapper>

);

export default Navigation