import React from 'react'
import styled from 'styled-components'
import { Link } from 'gatsby'

const SubnavigationWrapper = styled.ul`
    padding: 0;
    list-style: none;
    display: flex;
`;

const SubnavigationItem = styled.li`
    margin-right: 30px;  
    font-weight: 700;
    font-size: 1.5rem;

    a {
        text-decoration: none;
        color: rgba(255, 255, 255, 0.7);
        text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
    }

    a:hover {
        color: rgb(245, 251, 173);
    }

`;

const Subnavigation = () => (
    <SubnavigationWrapper>
        <SubnavigationItem><Link to={'/yoko/'}>yoko</Link></SubnavigationItem>
        <SubnavigationItem><Link to={'/sid/'}>sid</Link></SubnavigationItem>    
        <SubnavigationItem><Link to={'/marek/'}>marek</Link></SubnavigationItem>    
        <SubnavigationItem><Link to={'/uki/'}>uki</Link></SubnavigationItem>   
    </SubnavigationWrapper>

);

export default Subnavigation